import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            list:null,
            drMsg:this.$store.state.userInfo.Data.User,
            temp:false
        }
    },
    created(){
        this.getList()
    },
    mounted(){
    },
    methods:{
        getList(){
        	//获取医生的默认设置
            this.$get('PlatFormAPI/DoctorReferral/QueryDoctorDisGroup',{
                DrId:this.drMsg.DoctorId
            }).then(o => {
                this.list = o || []
                this.temp = true
            })
        },
        goSet(i){
        	this.$router.push({
        		path:"defaultDrTeam",
        		query:{
        			DisGroupId:i.DisGroupId,
        			DisGroupName:i.DisGroupName
        		}
        	})
        },
        goDise(){
        	this.$store.dispatch('pushSaveDiease',this.list)
        	this.$router.push('/dieaseList?fromReferralAdd=1')
        }
    }
}



